'use client';

import { useState, useCallback } from 'react';
import { FiUpload } from 'react-icons/fi';
import { useStatus } from '../context/StatusContext';

interface FileUploadProps {
  onUpload: (file: File) => Promise<{ port: number }>;
  isUploading: boolean;
}

export default function FileUpload({ onUpload, isUploading }: FileUploadProps) {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [error, setError] = useState('');
  const [inviteCode, setInviteCode] = useState<string | null>(null);
  const { setUploads, setSpeed } = useStatus();

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      setSelectedFile(e.target.files[0]);
      setError('');
      setInviteCode(null);
    }
  };

  const handleSubmit = useCallback(async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!selectedFile) {
      setError('Please select a file to upload');
      return;
    }

    try {
      // Update status before starting upload
      setUploads(prev => prev + 1);

      // Simulate upload progress (replace with actual progress callbacks from your P2P library)
      const progressInterval = setInterval(() => {
        const randomSpeed = Math.random() * 1.5 + 0.3; // Random speed between 0.3-1.8 MB/s
        setSpeed(prev => ({
          ...prev,
          upload: `${randomSpeed.toFixed(1)} MB/s`
        }));
      }, 1000);

      const result = await onUpload(selectedFile);
      setInviteCode(result.port.toString());

      // Clean up after upload completes
      clearInterval(progressInterval);
      setUploads(prev => prev - 1);
      setSpeed(prev => ({
        ...prev,
        upload: '0 KB/s'
      }));

    } catch (err) {
      setError('Failed to upload the file. Please try again.');
      setUploads(prev => prev > 0 ? prev - 1 : 0);
      setSpeed(prev => ({
        ...prev,
        upload: '0 KB/s'
      }));
    }
  }, [selectedFile, onUpload, setUploads, setSpeed]);

  return (
    <div className="space-y-4">
      <div className="bg-green-50 p-4 rounded-lg border border-green-100">
        <h3 className="text-lg font-medium text-green-800 mb-2">Share a File</h3>
        <p className="text-sm text-green-600 mb-0">
          Select a file to generate an invite code for sharing.
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label htmlFor="fileUpload" className="block text-sm font-medium text-gray-700 mb-1">
            File to Share
          </label>
          <input
            type="file"
            id="fileUpload"
            onChange={handleFileChange}
            className="w-full px-4 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent disabled:bg-gray-100 disabled:cursor-not-allowed"
            disabled={isUploading}
            required
          />
        </div>

        {inviteCode && (
          <div className="bg-blue-50 p-4 rounded-lg border border-blue-100">
            <h4 className="text-sm font-medium text-blue-800 mb-1">Invite Code Created</h4>
            <p className="text-sm text-blue-600 mb-0">
              Share this port number with the recipient:
            </p>
            <div className="mt-2 p-2 bg-white rounded border border-blue-200 text-center font-mono text-lg">
              {inviteCode}
            </div>
          </div>
        )}

        {error && <p className="mt-1 text-sm text-red-600">{error}</p>}

        <button
          type="submit"
          className="w-full px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-green-500 focus:ring-offset-2 disabled:bg-green-400 disabled:cursor-not-allowed flex items-center justify-center"
          disabled={isUploading || !selectedFile}
        >
          {isUploading ? (
            <span>Uploading...</span>
          ) : (
            <>
              <FiUpload className="mr-2" />
              <span>Share File</span>
            </>
          )}
        </button>
      </form>
    </div>
  );
}